.footerContainer {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  padding-top: 20px;
  align-items: center;
  margin-bottom: 40px;
  margin-left: 400px;
  margin-right: 400px;
}

.footerLogo {
  max-width: 200px;
}

.trademark {
  font-size: 18px;
  font-family: "CaviarDreams";
  font-weight: bolder;
  color: rgba(110, 82, 110, 0.863);
}

@media only screen and (max-width: 450px) {
  .footerContainer {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    padding-top: 20px;
    align-items: center;
    margin-bottom: 40px;
    margin-left: 100px;
    margin-right: 100px;
  }
}

@media only screen and (max-width: 950px) {
  .footerContainer {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    padding-top: 20px;
    align-items: center;
    margin-bottom: 40px;
    margin-left: 100px;
    margin-right: 100px;
  }
}
@media only screen and (max-width: 450px) {
  .footerLogo {
    max-width: 100px;
  }
}
