.headerContainer {
  display: flex;
  background-color: white;
  margin-bottom: 50px;
}

.emptyContainer {
  width: 33%;
}

.socialContainer {
  width: 33%;
  padding-top: 25px;
  margin-right: 30px;
  margin-left: 20px;
  justify-content: flex-end;
}

.twitterLogo {
  max-height: 60px;
  cursor: pointer;
}

.instagramLogo {
  max-height: 60px;
  margin-left: 30px;
  cursor: pointer;
}

@media only screen and (max-width: 450px) {
  .headerContainer {
    display: flex;
    background-color: white;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 450px) {
  .emptyContainer {
    width: 33%;
  }
}
@media only screen and (max-width: 450px) {
  .twitterLogo {
    max-height: 20px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 450px) {
  .instagramLogo {
    max-height: 20px;
    margin-left: 30px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 950px) {
  .twitterLogo {
    max-height: 25px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 950px) {
  .instagramLogo {
    max-height: 25px;
    margin-left: 30px;
    cursor: pointer;
  }
}
