.faqContainer {
  background-color: white;
}

.faqContent {
  display: flex;
  font-size: 50px;
  color: rgba(110, 82, 110, 0.863);
  font-weight: bold;
  justify-content: space-around;
  align-items: center;
}

.faq {
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

.FAQheader {
  max-width: 200px;
}
@media only screen and (max-width: 950px) {
  .faqImage {
    display: none;
  }
}

@media only screen and (max-width: 450px) {
  .faqText {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
    justify-content: center;
    align-items: flex-end;
  }
}

@media only screen and (max-width: 950px) {
  .faqText {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
    justify-content: center;
    align-items: flex-end;
  }
}

@media only screen and (max-width: 450px) {
  .faq {
    display: flex;
    justify-content: center;
    margin-top: 100px;
  }
}

@media only screen and (max-width: 450px) {
  .FAQheader {
    max-width: 100px;
