.bannerContainer {
  background-color: white;
  display: flex;
  margin-bottom: -3px;
  align-items: center;
  margin-left: 200px;
  margin-right: 200px;
  justify-content: center;
}

.bannerHeader {
  max-width: 1000px;
}

.DiscordContainer {
  justify-content: center;
  align-items: center;
  color: antiquewhite;
  text-decoration-color: black;
}

.discord {
  height: 100px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-family: "CaviarDreams";
  margin-bottom: 150px;
}

.braqIntro {
  width: 100%;
  color: #c6cde8;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.head {
  font-size: 120px;
  font-weight: bolder;
}

.bodyText {
  font-size: 26px;
  font-weight: bold;
}

@media only screen and (max-width: 450px) {
  .bannerContainer {
    background-color: white;
    display: flex;
  }
}
@media only screen and (max-width: 450px) {
  .bannerHeader {
    max-width: 200px;
  }
}

@media only screen and (max-width: 450px) {
