.utilityContainer {
  background-image: url("../assets/header/Braq_Web_Banner-03.png");
  align-items: center;
}

.utilityBanner {
  height: fit-content;
  display: flex;
  flex-direction: column-reverse;
  align-content: center;
}

.bannerImage {
  object-fit: contain;
  align-items: center;
}

.utilityHeader {
  font-size: 30px;
  font-weight: bold;
  color: rgba(110, 82, 110, 0.863);
  padding-top: 50px;
  margin-bottom: 10px;
}

.utilityBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: rgba(110, 82, 110, 0.863);
  margin-left: 400px;
  margin-right: 400px;
  margin-top: 100px;
}

.textHeader {
  font-size: 58px;
  font-weight: bolder;
  color: #f9c0c2;
  text-align: left;
}

.textBody {
  font-size: 24px;
  font-weight: normal;
  margin-top: 10px;
  margin-bottom: 30px;
  text-align: left;
}

.Definition {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.imagebox {
  max-height: 450px;
}

.treasuryFund {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

.tokenAirdrop {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

.Community {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

