.accordion {
  margin-bottom: 20px;
  padding: 50px;
  background-color: white;
  text-align: justify;
  align-items: center;
}

.accordion-item {
  padding-bottom: 20px;
  margin: 20px;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid;
}

.accordion-title {
  font-weight: bold;
  font-size: 26px;
  font-family: "CaviarDreams";
  color: rgba(110, 82, 110, 0.863);
}

.accordion-content {
  font-size: 16px;
  font-family: "CaviarDreams";
  color: rgba(105, 86, 105, 0.863);
  max-width: fit-content;
}
@media only screen and (max-width: 450px) {
  .accordion {
    margin-bottom: 20px;
    padding-top: 1px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: white;
    text-align: justify;
  }
}

@media only screen and (max-width: 450px) {
  .accordion-title {
    font-weight: bold;
    font-size: 18px;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
  }
}

@media only screen and (max-width: 450px) {
  .accordion-content {
    font-size: 12px;
    font-family: "CaviarDreams";
    color: rgba(105, 86, 105, 0.863);
  }
}

@media only screen and (max-width: 950px) {
  .accordion-title {
    font-weight: bold;
    font-size: 18px;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
  }
}

@media only screen and (max-width: 950px) {
  .accordion-content {
    font-size: 12px;
    font-family: "CaviarDreams";
    color: rgba(105, 86, 105, 0.863);
  }
}
